import { Component, OnInit } from '@angular/core';
import samples_docs from "../assets/samples.json";


export enum Tabs {
	overview,
	apireference,
	samples
};

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})

export class AppComponent {
	title = 'app';
	CurrentTab: Tabs;
	TabTypes = Tabs;

	public get sample(): string {
		return samples_docs['ixnetwork_restpy.tests.getting_started.py'];
    }

	constructor() { }

	async ngOnInit() {
		this.CurrentTab = Tabs.overview;
	}	
}
